import Vue from 'vue'
import VueRouter from 'vue-router'
import Aview from "../views/Aview.vue"
import Bview from "../views/Bview.vue"
import Kview from "../views/Kview.vue"
// 在这里引入路由组件

Vue.use(VueRouter)

const routes = [
    // 在这里配置路由
    {
        path:"/",
        component:Kview
    },
    {
        path:"/a",
        component:Aview
    },
    {
        path:"/b",
        component:Bview
    }
]

const router = new VueRouter({
    routes
})

export default router
